import { Component, OnInit } from '@angular/core';

// Services
import { CartoesConsultaService } from 'src/app/core/services/cartoes.service';
import { EmitEvent, EventBusService, Events } from 'src/app/core/services/eventbus.service';

@Component({
  selector: 'movida-cartao-credito-novo-verso',
  templateUrl: './cartao-credito-novo-verso.component.html',
  styleUrls: ['./cartao-credito-novo-verso.component.scss']
})
export class CartaoCreditoNovoVersoComponent implements OnInit {

  constructor(
    public cartoesConsultaService: CartoesConsultaService,
    private eventBusService: EventBusService
  ) { }

  ngOnInit(): void {}

  public cartaoCreditoCVVEvent(cvv: number): void {
    this.eventBusService.emit(new EmitEvent(Events.sendCVV, cvv));
  }

  public alertDuvida() {
    // this.cartaoService.alert.success('Cód. Segurança*', 'O código de segurança é \
    //                                   composto por um número de três ou quatro dígitos no \
    //                                   verso do cartão de crédito.');
  }

}
